"use client"
/* eslint-disable react-hooks/rules-of-hooks */
import React, { useState } from "react";
import { Button } from "@/components/ui/button";
import { Sheet, SheetContent, SheetTrigger } from "@/components/ui/sheet";
import { Input } from "@/components/ui/input";
import {
  Table,
  TableBody,
  TableCaption,
  TableCell,
  TableHead,
  TableHeader,
  TableRow,
} from "@/components/ui/table";

type UserRow = {
  id: number;
  user: string;
  gender: string;
  telephone: string;
  email:string;
  addedBy: string;
  type: string;
  serverId: number;
};

const page = () => {
  const [data, setData] = useState<UserRow[]>([
    { id: 3, user: "User", gender: "Female", telephone: "123-456-7890", email: "user@example.com", addedBy: "Nassim", type: "admin", serverId: 0 },
    { id: 4, user: "Nessim", gender: "Male", telephone: "234-567-8901", email: "nessim@example.com", addedBy: "User", type: "admin", serverId: 1 },
    { id: 1004, user: "Kayondo", gender: "Male", telephone: "345-678-9012", email: "kayondo@example.com", addedBy: "User", type: "normal", serverId: 2 },
    { id: 2004, user: "John", gender: "Male", telephone: "456-789-0123", email: "john@example.com", addedBy: "User", type: "normal", serverId: 3 },
    { id: 3004, user: "Werps", gender: "Male", telephone: "567-890-1234", email: "werps@example.com", addedBy: "User", type: "normal", serverId: 4 },
  ]);

  const [editRow, setEditRow] = useState<UserRow | null>(null);

  const handleEdit = (id: number) => {
    const row = data.find((d) => d.id === id);
    if (row) setEditRow(row);
  };

  const handleDelete = (id: number) => {
    setData(data.filter((row) => row.id !== id));
  };

  const handleSave = () => {
    if (editRow) {
      setData(data.map((row) => (row.id === editRow.id ? editRow : row)));
      setEditRow(null); // Close the sheet
    }
  };

  return (
    <div className="p-6">
      <div className="flex justify-between items-center mb-4">

      <h2 className="text-lg font-bold mb-4">Manage Users</h2>

        <div className="space-x-4">
            <Button className="bg-blue-600 hover:bg-blue-700">
              <i className="fas fa-plus mr-2"></i>Add
            </Button>
            <Button className="bg-blue-600 hover:bg-blue-700">
              <i className="fas fa-print mr-2"></i>Print
            </Button>
            <Button className="bg-blue-600 hover:bg-blue-700">
              <i className="fas fa-file-excel mr-2"></i>Export To Excel
            </Button>
        </div>
        
      </div>

      <Table>
        <TableCaption>A list of users and their details</TableCaption>
        <TableHeader>
          <TableRow>
            <TableHead>ID</TableHead>
            <TableHead>User</TableHead>
            <TableHead>Gender</TableHead>
            <TableHead>Telephone</TableHead>
            <TableHead>Email</TableHead>
            <TableHead>Added By</TableHead>
            <TableHead>Type</TableHead>
            <TableHead>Server ID</TableHead>
            <TableHead>Actions</TableHead>
          </TableRow>
        </TableHeader>
        <TableBody>
          {data.map((row) => (
            <TableRow key={row.id}>
              <TableCell>{row.id}</TableCell>
              <TableCell>{row.user}</TableCell>
              <TableCell>{row.gender}</TableCell>
              <TableCell>{row.telephone}</TableCell>
              <TableCell>{row.email}</TableCell>
              <TableCell>{row.addedBy}</TableCell>
              <TableCell>{row.type}</TableCell>
              <TableCell>{row.serverId}</TableCell>
              <TableCell>
                <div className="flex space-x-2">
                  <Sheet>
                    <SheetTrigger asChild>
                      <Button size="sm" variant={'secondary'} onClick={() => handleEdit(row.id)}>Edit</Button>
                    </SheetTrigger>
                    <SheetContent className="bg-primary dark:bg-primary-dark">
                      <div className="p-4">
                        <h2 className="text-lg font-bold mb-4">Edit Row</h2>
                        {editRow && (
                          <>
                            <div className="mb-4">
                              <label>ID:</label>
                              <Input
                                value={editRow.id}
                                disabled
                                className="w-full mt-1"
                              />
                            </div>
                            <div className="mb-4">
                              <label>User:</label>
                              <Input
                                value={editRow.user}
                                onChange={(e) => setEditRow({ ...editRow, user: e.target.value })}
                                className="w-full mt-1"
                              />
                            </div>
                            <div className="mb-4">
                              <label>Gender:</label>
                              <Input
                                value={editRow.gender}
                                onChange={(e) => setEditRow({ ...editRow, gender: e.target.value })}
                                className="w-full mt-1"
                              />
                            </div>
                            <Button onClick={handleSave} className="mr-2 bg-gray-700 hover:bg-gray-600">Save</Button>
                          </>
                        )}
                      </div>
                    </SheetContent>
                  </Sheet>
                  <Button

                    size="sm"
                    variant="destructive"
                    onClick={() => handleDelete(row.id)}
                  >
                    Delete
                  </Button>
                </div>
              </TableCell>
            </TableRow>
          ))}
        </TableBody>
      </Table>
    </div>
  );
};

export default page;
